:root {
  --text: #fff;
  --background: #000;
  --link: DeepSkyBlue;
  --additional-text: #eaecef;

  --font-size-S: 14px;
  --font-size: 16px;
  --font-size-L: 22px;
  --font-size-XL: 30px;
  --font-size-XXL: 44px;
  --font-size-XXXL: 64px;

  --vertical-gap: 7vh;
  --horizontal-gap: 5vw;

  --block-border: solid 1px rgb(47, 51, 54);
  --input-border: solid 2px rgb(47, 51, 54);
}

@media (max-width: 600px) {
  :root {
    --font-size-S: 10px;
    --font-size: 12px;
    --font-size-L: 16px;
    --font-size-XL: 21px;
    --font-size-XXL: 28px;
    --font-size-XXXL: 46px;
  }
}

body {
  margin: 0;
  font-family: "JetBrains Mono", Consolas, monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: var(--font-size);
  line-height: 1.5;
  color: var(--text);
  background-color: var(--background);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  color: var(--link);
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

@media (max-width: 600px) {
  br {
    content: ' ';
  }

  br:after {
    content: ' ';
  }
}

h1 {
    font-size: var(--font-size-XXL);
    font-weight: bold;
    margin: 0 auto 0.5em;
    text-align: center;
}

h2 {
    font-size: var(--font-size-XL);
    font-weight: bold;
    margin: 0 auto 1em;
    text-align: center;
}

h3 {
    font-size: var(--font-size-L);
    font-weight: bold;
    margin: 0 auto 0.5em;
}

form {
    background: rgb(21, 24, 28);
    box-sizing: border-box;
    border: var(--block-border);
    border-radius: 14px;
    padding: 1.5em 2em;
}

form label {
    font-size: var(--font-size);
}

form input {
    border: var(--input-border);
    border-radius: 14px;
    box-sizing: border-box;
    background: var(--background);
    color: var(--text);
    font-family: "JetBrains Mono", Consolas, monospace;
    font-size: var(--font-size);
    margin: 0;
    padding: 0.5em 1em;
    width: 100%;
}

form textarea {
    border: var(--input-border);
    border-radius: 14px;
    box-sizing: border-box;
    background: var(--background);
    color: var(--text);
    font-family: "JetBrains Mono", Consolas, monospace;
    font-size: var(--font-size);
    margin: 0;
    padding: 0.5em 1em;
    width: 100%;
}

form select {
    appearance:none;
    border: var(--input-border);
    border-radius: 14px;
    box-sizing: border-box;
    background: var(--background);
    color: var(--text);
    font-family: "JetBrains Mono", Consolas, monospace;
    font-size: var(--font-size);
    margin: 0;
    padding: 0.5em 1em;
    width: 100%;
}

form button {
    border: var(--input-border);
    border-radius: 14px;
    box-sizing: border-box;
    background: rgb(21, 24, 28);
    color: var(--text);
    cursor: pointer;
    font-family: "JetBrains Mono", Consolas, monospace;
    font-size: var(--font-size);
    margin: 0;
    padding: 0.5em 1em;
}

form button:hover {
    background: rgb(47, 51, 54);
}